<template>
  <div>
    <h3>components folder:</h3>
    <div v-for="(item,index) in componentsFolder" :key="index">
      <div v-if="typeof item !== 'string'">
        {{Object.keys(item)[0]}}
        <div v-if="isArray(item)">
          <div v-for="(fileName,index) in Object.values(item)[0]" :key="index">
            <div v-if="Object.keys(fileName).length === 1">{{'&nbsp;'.repeat(4)}}{{Object.keys(fileName)[0]}}</div>
            <div v-if="isArray(fileName)">
              <div v-for="(fileName,index) in Object.values(fileName)[0]" :key="index">
                <div v-if="isArray(fileName)"></div>
                <p class="colored" v-else @click="getFileName">{{'&nbsp;'.repeat(8)}}{{fileName}}</p>
              </div>
            </div>
            <p class="colored" v-else @click="getFileName">{{'&nbsp;'.repeat(4)}}{{fileName}}</p>
          </div>
        </div>
      </div>
      <p class="colored" v-else @click="getFileName">{{item}}</p>
    </div>
  </div>
</template>

<script>
import {componentsFolder} from "../arrHolder/componentsFolder.js";

export default {
  name: "HelloWorld",
  data() {
    return {
      componentsFolder,
    };
  },
  methods: {
    isArray(item) {
      return Array.isArray(Object.values(item)[0]);
    },
    getFileName() {
      this.$emit('getFileName', event.target.innerText.trim());
    }
  },
};
</script>

<style lang="scss" scoped>
.colored {
  color: #a367fc;
}
</style>
