<template>
  <div>
    <h3>scr folder:</h3>
    <div v-for="(file,index) in scrFolder" :key="index">
      <div v-if="typeof file !== 'string'">
        {{Object.keys(file)[0]}}
        <div
          v-for="(file,index) in Object.values(file)[0]"
          :key="index"
          @click="getFileName"
          class="colored"
        >{{'&nbsp;'.repeat(4)}}{{file}}</div>
      </div>
      <div class="colored" v-else>{{file}}</div>
    </div>
  </div>
</template>

<script>
import { scrFolder } from "../arrHolder/scrFolder.js";

export default {
  name: "SrcFolder",
  data() {
    return {
      scrFolder
    };
  },
  methods: {
    getFileName() {
      this.$emit('getFileName', event.target.innerText.trim());
    }
  }
};
</script>

<style lang="scss" scoped>
.colored {
  color: #a367fc;
}
</style>